<template lang="pug">
.curso-main-container.pb-3
  BannerInterno
  .container.tarjeta.tarjeta--blanca.p-4.p-md-5.mb-5
    .titulo-principal.color-acento-contenido
      .titulo-principal__numero
        span 2
      h1 Demanda turstica

    .bloque-texto-g.color-primario.p-3.p-sm-4.p-md-5
      .bloque-texto-g__img(
        :style="{'background-image': `url(${require('@/assets/curso/tema2/1.png')})`}"
      )
      .bloque-texto-g__texto.p-5.mt-4.mb-4
        p.mb-0 La demanda turstica se refiere a la cantidad de bienes y servicios que los consumidores desean adquirir a distintos precios, de acuerdo con la capacidad adquisitiva de los turistas.

    p.mt-5(data-aos="fade-up") A continuacin se brinda informacin para conocer ms sobre algunas definiciones sobre la demanda turstica.     

    SlyderA.slydera1.mt-5(tipo="b")
      .row.p-4
        .col-lg-4.mb-4.mb-lg-0
          h4 Libre juego de oferta y demanda.
          p.mt-5 De acuerdo con Landi (2013), la demanda turstica opera en el contexto de un mercado en el que intervienen diversos operadores de turismo ofreciendo determinados productos y servicios, y que supone, adems, la existencia de un conjunto de espacios geogrficos donde se realizan estos servicios. Esto sucede en el marco de un libre juego de oferta y demanda.
        .col-lg-8
          figure
            img(src='@/assets/curso/tema2/3.png', alt='')
      .row.p-4
        .col-lg-4.mb-4.mb-md-0
          h4 Demanda turstica segn los tericos.
          p.mt-5 Uno de los tericos ms estudioso del territorio y a demanda turstica, Robert Boulln (1997), afirma que el concepto de demanda turstica puede variar segn las escuelas en las que se inscriben los tericos tursticos. 
        .col-lg-8
          figure
            img(src='@/assets/curso/tema2/4.png', alt='')
      .row.p-4
        .col-lg-4.mb-4.mb-lg-0
          h4 Demanda del sector turstico.
          p.mt-5 Para l, por ejemplo, la demanda turstica se puede definir de dos formas:
          ul.lista-ul.mt-3(style="position:relative")
            li(data-aos="fade-up")
              p #[i.fas.fa-paper-plane(style="color:#FF5900;border: 1px red solid;border-radius:50%;padding: .2rem;font-size:10px;")] Cantidad de personas que viajan.
            li(data-aos="fade-up")
              p #[i.fas.fa-paper-plane(style="color:#FF5900;border: 1px red solid;border-radius:50%;padding: .2rem;font-size:10px;")] Consumo e ingresos de la zona turstica.
        .col-lg-8
          figure
            img(src='@/assets/curso/tema2/5.png', alt='')
      .row.p-4
        .col-lg-4.mb-4.mb-lg-0
          h4 Total de personas que viajan.
          p.mt-5 La primera, como el nmero total de personas que viajan o desean viajar para disfrutar de facilidades tursticas y de servicios, en lugares distintos al sitio de trabajo y residencia habitual.
        .col-lg-8
          figure
            img(src='@/assets/curso/tema2/6.png', alt='')
      .row.p-4
        .col-lg-4.mb-4.mb-lg-0
          h4 Ingresos de la zona turstica
          p.mt-5 La segunda, como el consumo y los ingresos que se generan en la zona turstica. Este es un concepto ms econmico que est expresado en unidades monetarias y no en nmero de consumidores.
        .col-lg-8
          figure
            img(src='@/assets/curso/tema2/7.png', alt='')

    Separador

    .titulo-segundo
      h2#t_2_1 2.1 Perfil de turista

    .row
      .col-md-4.mb-4(style="place-self:center;")
        figure
          img(src="@/assets/curso/tema2/8.png" data-aos="fade-right")
      .col-md-8(style="place-self:center;")
        .cajon.color-primario.p-5.mt-4(style="background-color:#FFD667;box-shadow:0px 0px 20px rgba(0,0,0,.14);")
          p.mb-0(data-aos="fade-up" style="position:relative") El turista moderno, dista mucho del turista de hace 20 aos debido, entre otras cosas, a los cambios tecnolgicos, polticos, econmicos y culturales, los estudios que dan cuenta del perfil del turista, hoy se hacen con mayor frecuencia que antes, ya que es la mejor manera para atender apropiadamente la demanda en crecimiento del sector turstico en el mundo. El nuevo turista no viaja en grupos, primero elige el destino, luego el hotel y el vuelo, y lo dems lo elige y compra en el destino.
        p.mt-4(data-aos="fade-up") En un reciente informe de la Organizacin Mundial del Turismo (OMT-2020), analiza los cambios demogrficos que afectarn al turismo en el siglo XXI y sostiene que:
        ul.lista-ul.mt-3(style="position:relative")
          li(data-aos="fade-up")
            p #[i.fas.fa-paper-plane(style="color:#FF5900;border: 1px red solid;border-radius:50%;padding: .2rem;font-size:10px;")] La progresiva concentracin humana en grandes reas urbanas marcar la evolucin demogrfica y afectar al turismo de aqu hasta 2030.
          li(data-aos="fade-up")
            p #[i.fas.fa-paper-plane(style="color:#FF5900;border: 1px red solid;border-radius:50%;padding: .2rem;font-size:10px;")] El turista es quien manda y la oferta es la que se debe adaptar a un cliente que exige informacin veraz, la mejor relacin calidad-precio, experiencias autnticas y destinos que ofrezcan una oferta turstica adecuada a sus deseos y necesidades.

    p.mt-2(data-aos="fade-up") En su informe para la OMT los turistas presentan varias caractersticas en su comportamiento, a saber:

    .row.mt-5
      .col-md-8.mb-4.mb-md-0
        SlyderA(style="background-color:#FFE6A1;border-radius:20px;height:100%;")
          div.p-5
            .row
              .col-auto(style="place-self:center")
                figure
                  img(src="@/assets/curso/tema2/10.png")
              .col(style="place-self:center")
                p.mb-0 Son tecnolgico-dependientes: sin importar la edad, usan la tecnologa para obtener asistencia durante el viaje de principio a fin.
            .row.mt-3
              .col-auto(style="place-self:center")
                figure
                  img(src="@/assets/curso/tema2/11.png")
              .col(style="place-self:center")
                p.mb-0 Buscan personalizacin en las experiencias: es el ao perfecto para el uso de datos al servicio de los viajeros.
            .row.mt-3
              .col-auto(style="place-self:center")
                figure
                  img(src="@/assets/curso/tema2/12.png")
              .col(style="place-self:center")
                p.mb-0 Estn ms conscientes del impacto que causa su actividad: tanto en la comunidad local como en la ecologa. Por tanto, tiende a realizar actividades sustentables.
          div.p-5
            .row
              .col-auto(style="place-self:center")
                figure
                  img(src="@/assets/curso/tema2/13.png")
              .col(style="place-self:center")
                p.mb-0 Quieren destinos novedosos: buscan en las agencias de viaje, tanto tradicionales como online, para encontrar experiencias curadas y realmente novedosas.
            .row.mt-3
              .col-auto(style="place-self:center")
                figure
                  img(src="@/assets/curso/tema2/14.png")
              .col(style="place-self:center")
                p.mb-0 El turista de hoy es diversificado, sofisticado y bien informado, causando nuevas necesidades que los profesionales de la industria deben enfrentar. 
            .row.mt-3
              .col-auto(style="place-self:center")
                figure
                  img(src="@/assets/curso/tema2/15.png")
              .col(style="place-self:center")
                p.mb-0 Los nuevos turistas se interesan por encontrar apps que les permitan identificar destinos donde el aumento de turistas tendra un efecto positivo en la comunidad local.
          div.p-5
            .row
              .col-auto(style="place-self:center")
                figure
                  img(src="@/assets/curso/tema2/16.png")
              .col(style="place-self:center")
                p.mb-0 El turista tecnolgico, tiene un perfil que utiliza constantemente las redes sociales y, en general, las nuevas tecnologas, para tomar decisiones y recibir sugerencias a medida.
            .row.mt-3
              .col-auto(style="place-self:center")
                figure
                  img(src="@/assets/curso/tema2/17.png")
              .col(style="place-self:center")
                p.mb-0 Consulta referencias para elegir el alojamiento o el restaurante donde ir a cenar. 
            .row.mt-3
              .col-auto(style="place-self:center")
                figure
                  img(src="@/assets/curso/tema2/18.png")
              .col(style="place-self:center")
                p.mb-0 Utilizan las apps para organizar previamente las actividades y poder tener todas las informaciones relativas al viaje en un nico lugar.
          div.p-5
            .row
              .col-auto(style="place-self:center")
                figure
                  img(src="@/assets/curso/tema2/19.png")
              .col(style="place-self:center")
                p.mb-0 El viajero con mascota es otro tipo de perfil cada vez ms comn, tanto que el nmero de alojamientos que admiten mascotas ha ido aumentando con el transcurso de los aos, por ello las estructuras tursticas buscan ofrecer cada vez ms servicios para las mascotas, as como propuestas a medida como camas para perro gratuitas, spas y tratamientos para las mascotas, entre otros.
            .row.mt-3
              .col-auto(style="place-self:center")
                figure
                  img(src="@/assets/curso/tema2/20.png")
              .col(style="place-self:center")
                p.mb-0 Uno de los efectos de la COVID-19, en los turistas es su afanosa bsqueda de nuevos destinos que, en todo caso, garanticen su seguridad sanitaria.

      .col-md-4(style="place-self:center")
        figure
          img(src="@/assets/curso/tema2/9.png" data-aos="fade-left")

    Separador

    .titulo-segundo
      h2#t_2_2 2.2 Demanda turstica y relacin con el guionaje

    p(data-aos="fade-up") La demanda turstica guarda relacin muy importante con la guianza turstica, como se caracteriza a continuacin:

    .row.mt-5
      .col-md-4.mb-4(style="place-self:center")
        figure
          img(src="@/assets/curso/tema2/21.png" data-aos="fade-right")
      .col-md-8(style="place-self:center")
        ul.lista-ul.mt-3(style="position:relative")
          li(data-aos="fade-up")
            p #[i.fas.fa-paper-plane(style="color:#FF5900;border: 1px red solid;border-radius:50%;padding: .2rem;font-size:10px;")] Los destinos apetecidos por los turistas cuentan con caractersticas: culturales, patrimoniales, econmicas, polticas y medioambientales de importancia, para lo cual requiere de asesora sobre todos estos temas, pero tambin la instruccin necesaria para adquirir conocimientos.
          li(data-aos="fade-up")
            p #[i.fas.fa-paper-plane(style="color:#FF5900;border: 1px red solid;border-radius:50%;padding: .2rem;font-size:10px;")] Lo mnimo que espera un turista del destino que visitar es encontrar que, adems de buen alojamiento y alimentacin, se encuentre en ese espacio turstico con personal idneo, paciente, bueno para escuchar y excelente para explicar, de preferencia en el idioma del turista.
          li(data-aos="fade-up")
            p #[i.fas.fa-paper-plane(style="color:#FF5900;border: 1px red solid;border-radius:50%;padding: .2rem;font-size:10px;")] Otro factor determinante para que quien demanda el servicio turstico quede satisfecho, son las asesoras por parte del gua, sobre el tema de compras, buenos precios y calidad, ya que siendo forneo el turista no identifica estos aspectos con facilidad.
          li(data-aos="fade-up")
            p #[i.fas.fa-paper-plane(style="color:#FF5900;border: 1px red solid;border-radius:50%;padding: .2rem;font-size:10px;")] Al final del viaje, cuando el turista mide su grado de satisfaccin respecto del destino que visit, una de los temas que ms tendr en cuenta es su interaccin con el gua que fue su contacto fsico permanente, que lo asisti, le ayud con trmites, lo instruy y lo gui en cada parte del viaje.

    Separador

    .titulo-segundo
      h2#t_2_3 2.3 Demanda turstica: principios generales

    .row
      .col-md-8.mb-4(style="place-self:center")
        p(data-aos="fade-up") Para brindar una excelente oferta de productos a los turistas que demandan ser satisfechos por los prestadores de servicios tursticos, se debe entender a los consumidores tursticos, por ello, vale la pena hablar de los principios bsicos del consumidor.
        p.mt-4(data-aos="fade-up") El comportamiento del consumidor de productos tursticos est relacionado con todas las actividades que lleva a cabo una persona desde que experimenta la necesidad de viajar hasta el momento en que efecta la compra y utiliza los distintos servicios tursticos.
        p.mt-4(data-aos="fade-up") As las cosas, el comportamiento del consumidor es el siguiente:
        ul.lista-ul.mt-3(style="position:relative")
          li(data-aos="fade-up")
            p #[i.fas.fa-paper-plane(style="color:#FF5900;border: 1px red solid;border-radius:50%;padding: .2rem;font-size:10px;")] Surgimiento de la necesidad de viajar.
          li(data-aos="fade-up")
            p #[i.fas.fa-paper-plane(style="color:#FF5900;border: 1px red solid;border-radius:50%;padding: .2rem;font-size:10px;")] Bsqueda de informacin.
          li(data-aos="fade-up")
            p #[i.fas.fa-paper-plane(style="color:#FF5900;border: 1px red solid;border-radius:50%;padding: .2rem;font-size:10px;")] Evaluacin de alternativas.
          li(data-aos="fade-up")
            p #[i.fas.fa-paper-plane(style="color:#FF5900;border: 1px red solid;border-radius:50%;padding: .2rem;font-size:10px;")] Seleccin de una alternativa y compra efectiva sensaciones posteriores a la compra.
      .col-md-4(style="place-self:center")
        figure
          img(src="@/assets/curso/tema2/22.png" data-aos="fade-left")

    p.mt-2(data-aos="fade-up") Todos estos aspectos deben ser tenidos en cuenta por los PST (Plan Sectorial de Turismo), a la hora de ofertar un producto o servicio y les permiten realizar un ejercicio muy importante, el de segmentar el mercado al que le ofrecer sus bienes y servicios.

    .cajon.color-secundario.p-4.mt-5.row(style="background-color:#B7CEAC;")
      .col-md-2.mb-4.mb-md-0(style="place-self:center")
        figure.text-center
          img(src="@/assets/curso/tema2/23.png" data-aos="zoom-in-up" style="display:inline;max-width:100px;")
      .col-md-10(style="place-self:center")
        p.mb-0(data-aos="fade-up") Serra (2003), indica que la segmentacin del mercado, es el proceso mediante el cual se divide el mercado en subgrupos relativamente homogneos para llevar a cabo una estrategia comercial diferenciada y satisfacer de manera ms eficiente sus necesidades, dentro de este anlisis siempre se deber tener en cuenta la prestacin del servicio de guionaje, tan importante para llenar de experiencias positivas al turista, con el fin que cumpla todo el ciclo turstico.

    Separador

    .titulo-segundo
      h2#t_2_4 2.4 Producto turstico: concepto, caractersticas

    p(data-aos="fade-up") El producto turstico constituye la piedra angular del mercado turstico y su diseo es la actividad ms importante del #[em marketing].

    .bloque-texto-g.color-secundario.p-3.p-sm-4.p-md-5.mt-5
      .bloque-texto-g__img(
        :style="{'background-image': `url(${require('@/assets/curso/tema2/24.png')})`}"
      )
      .bloque-texto-g__texto.p-4
        p.mb-0(data-aos="fade-up") El producto turstico, de acuerdo con la Organizacin Mundial del Turismo (1998), debe ser ms flexible con el propsito de adaptarse a los gustos y necesidades de los turistas, porque cada vez aparece nuevos destinos tursticos que se posicionan rpidamente, en el mercado gracias a sus atributos geogrficos. Esto obliga a los destinos tradicionales a innovar y repensar la actividad turstica, en beneficio del turista y el dinamismo del sector.

    p.mt-5(data-aos="fade-up") Es muy importante que, al #[strong(style="color:#EB6F2D;") identificar un producto turstico y que se desea poner en el mercado turstico, se evalen muy bien los conceptos sobre el mismo;] aqu algunos de ellos:

    .row.mt-5
      .col-md-6.mb-4(style="place-self:center")
        ul.lista-ul.mt-3(style="position:relative")
          li(data-aos="fade-up")
            p #[i.fas.fa-paper-plane(style="color:#FF5900;border: 1px red solid;border-radius:50%;padding: .2rem;font-size:10px;")] El producto turstico es el conjunto de atractivos, equipamientos, servicios, infraestructura y organizaciones que satisfacen una necesidad o un deseo de los consumidores tursticos. Recordando que un solo destino turstico puede contener varios productos tursticos.
          li(data-aos="fade-up")
            p #[i.fas.fa-paper-plane(style="color:#FF5900;border: 1px red solid;border-radius:50%;padding: .2rem;font-size:10px;")] Tambin un producto turstico est conformado por el conjunto de bienes y servicios que se ofrecen al mercado en forma individual o en una gama amplia de combinaciones resultantes de las necesidades, requerimientos o deseos de un consumidor al que llamamos turista.
        p.mt-4(data-aos="fade-up") A continuacin, se exponen las #[strong(style="color:#EB6F2D;") caractersticas de un producto turstico, donde se analiza el producto turstico como una experiencia en la cual son utilizados productos muy diversos para su satisfaccin:]
      .col-md-6(style="place-self:center")
        figure
          img(src="@/assets/curso/tema2/25.png" data-aos="fade-left")

    .cont_tema2_1.mt-5.ps-3.pe-3.pt-4.pb-4(style="position:relative")
      ImagenInfograficaBAjustada.color-primario.col-md-8.m-auto
        template(v-slot:imagen)
          figure
            img(src='@/assets/curso/tema2/27.svg' data-aos="zoom-in-up")
        div(x="30%" y="30%" tooltip="Inseparabilidad" numero="Inseparabilidad")
          .row
            .col-md-8
              h4(style="color:#12263F !important;") Inseparabilidad
              p.mt-3(style="color:#12263F !important;") La produccin y el consumo son simultneos, se efectan al mismo tiempo y se dan en las instalaciones del oferente.
            .col-md-4
              figure
                img(src="@/assets/curso/tema2/28.png")
        div(x="70%" y="30%" tooltip="Heterogeneidad" numero="Heterogeneidad")
          .row
            .col-md-8
              h4(style="color:#12263F !important;") Heterogeneidad
              p.mt-3(style="color:#12263F !important;") Los servicios pueden ser altamente variables ya que dependen de la persona que los suministra, de la persona que lo recibe y de las circunstancias del momento.
            .col-md-4
              figure
                img(src="@/assets/curso/tema2/29.png")
        div(x="20%" y="50%" tooltip="Intangibilidad" numero="Intangibilidad")
          .row
            .col-md-8
              h4(style="color:#12263F !important;") Intangibilidad
              p.mt-3(style="color:#12263F !important;") Los servicios son intangibles, aunque no lo sean los elementos que sirven de soporte para la prestacin del servicio. Esta naturaleza intangible de los servicios presenta una serie de implicaciones para su comercializacin, por ejemplo, la dificultad para comercializarlo y el grado de incertidumbre para el consumidor antes de la adquisicin.
            .col-md-4
              figure
                img(src="@/assets/curso/tema2/30.png")
        div(x="80%" y="50%" tooltip="Perecibilidad" numero="Perecibilidad")
          .row
            .col-md-8
              h4(style="color:#12263F !important;") Perecibilidad
              p.mt-3(style="color:#12263F !important;") Los servicios tursticos no pueden almacenarse y por tanto son caducos. Un exceso de capacidad no vendida un periodo de tiempo concreto representa una prdida de ingresos potenciales que no puede ser recuperada.
            .col-md-4
              figure
                img(src="@/assets/curso/tema2/31.png")
        div(x="50%" y="70%" tooltip="Estacionalidad" numero="Estacionalidad")
          .row
            .col-md-8
              h4(style="color:#12263F !important;") Estacionalidad
              p.mt-3(style="color:#12263F !important;") Son los periodos de tiempo en los que ocurre la actividad turstica y que adems determina el tipo de estrategia para la venta del producto turstico, ya que este se consume particularmente en poca vacacional. 
              p.mt-4(style="color:#12263F !important;") Otros factores que tiene que ver con la estacionalidad, son los desplazamientos relacionados con celebraciones culturales, tradicionales como la Navidad, Ao Nuevo, Semana Santa, entre otros.
            .col-md-4
              figure
                img(src="@/assets/curso/tema2/31.png")

    Separador.mt-5

    .cont_tema2_2.pb-5.mt-5(style="position:relative")
      .titulo-segundo(style="position:relative")
        h2#t_2_5 2.5 Catlogo de productos tursticos de Colombia 
      .bloque-texto-g.color-secundario.p-3.p-sm-4.p-md-5.mt-5
        .bloque-texto-g__img(
          :style="{'background-image': `url(${require('@/assets/curso/tema2/34.png')})`}"
        )
        .bloque-texto-g__texto.p-5.mt-4.mb-4
          p.mb-0(data-aos="fade-up") Colombia es un pas con inmensas riquezas naturales, culturales, arquitectnicas, arqueolgicas, culturales patrimoniales y naturales que le hacen sobresalir, adicionalmente cuenta con una biodiversidad de flora y fauna que se destaca a nivel mundial. De all la importancia de revisar el catlogo de productos tursticos del pas.

      .mt-5.pt-5(style="box-shadow:0px 0px 10px rgba(0,0,0,.2);position:relative;border-radius:20px;background-color:white;")
        .cont_fake1
          .col-md-11.m-auto.mt-3(style="position:relative")
            span(data-aos="fade-up" style="padding:.7rem 2rem;background-color:#FCC843;font-size:20px;border-radius:50px;position:relative;font-weight:bold;") Productos tursticos de Colombia
          .col-md-7.m-auto.mt-5
            figure
              img(src="@/assets/curso/tema2/36.png" data-aos="zoom-in-up")

        .row.mt-5.ps-5.pe-5(style="position:relative")
          .col-auto
            figure
              img(src="@/assets/curso/tema2/37.png" data-aos="fade-up")
          .col
            span(style="font-size:18px;border-bottom:2px #DC753F solid;font-weight:bold;" data-aos="fade-up") Turismo cultural

        .row.ps-5.pe-5(style="position:relative")
          .col-md-6.mb-4(style="place-self:center")
            p.mt-5(data-aos="fade-up") Segn la OMT (2018), el #[strong(style="color:#EB6F2D;") turismo cultural es un tipo de actividad turstica en la que la motivacin esencial del visitante es aprender, descubrir, experimentar y consumir atractivos o productos culturales tangibles e intangibles en un destino turstico".] Segn Fontur (2016) en promedio el gasto asociado al turismo cultural oscila entre USD 1.870 y USD 3.140, dependiendo del destino.
            p.mt-4(data-aos="fade-up") El turismo cultural se proyecta como un impulsor de las empresas creativas, para el desarrollo de la economa naranja, las expresiones y bienes del patrimonio material e inmaterial, las festividades y eventos, as como el etnoturismo que cada da gana ms adeptos.
            p.mt-4(data-aos="fade-up") A continuacin, se expone ms informacin sobre la oferta de productos y subproductos que se encuentran catalogados como turismo cultural en Colombia:
          .col-md-6(style="place-self:center")
            figure
              img(src="@/assets/curso/tema2/38.png" data-aos="fade-left")
        .cont_fake2.mt-5  
          .ps-5.pe-5
            figure
